import { configureStore } from '@reduxjs/toolkit';
import { TypedUseSelectorHook, useDispatch as useBaseDispatch, useSelector as useBaseSelector } from 'react-redux';
import gameReducer from './game/gameSlice';
import { GameState } from './game/types';
import isEqual from 'lodash/isEqual';

const saveStateToLocalStorage = (state: RootState) => {
  try {
    const serializedState = JSON.stringify(state);
    localStorage.setItem('gameState', serializedState);
  } catch (e) {
    console.error('Не вдалося зберегти стан у localStorage', e);
  }
};

const loadStateFromLocalStorage = () => {
  try {
    const serializedState = localStorage.getItem('gameState');
    if (serializedState === null) return undefined;
    return JSON.parse(serializedState);
  } catch (e) {
    console.error('Не вдалося завантажити стан з localStorage', e);
    return undefined;
  }
};

const preloadedState: { game: GameState } | undefined = loadStateFromLocalStorage();

export const store = configureStore({
  reducer: {
    game: gameReducer,
  },
  preloadedState,
});

let previousState: RootState | undefined;

store.subscribe(() => {
  const currentState = store.getState();
  if (!isEqual(currentState, previousState)) {
    saveStateToLocalStorage(currentState);
    previousState = currentState;
  }
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

export const useDispatch = () => useBaseDispatch<AppDispatch>();
export const useSelector: TypedUseSelectorHook<RootState> = useBaseSelector;