import { useSelector, useDispatch, gameActions } from "../../store";
import "./PlayerStats.css";

const PlayerStats = () => {
  const players = useSelector((state) => state.game.players);
  const dispatch = useDispatch();

  if (players.length === 0) {
    return <p>Немає гравців для відображення.</p>;
  }

  return (
    <div className="player-stats">
      <h3>Статистика гравців</h3>
      <ul>
        {players.map((player, index) => (
          <li key={index}>
            {player.name}: {player.guessedWords} вгаданих слів
            <button
              style={{ marginLeft: 10, color: "red", cursor: "pointer" }}
              onClick={() => dispatch(gameActions.removePlayer({ name: player.name }))}
            >
              Видалити
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default PlayerStats;