import { useNavigate } from "react-router-dom";
import { useForm, SubmitHandler } from "react-hook-form";
import { useDispatch, useSelector } from "../store";
import { gameActions } from "../store/game/gameSlice";

interface FormData {
  playerName: string;
}

const StartPage = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const players = useSelector((state) => state.game.players);
  const { register, handleSubmit, formState: { errors }, setError } = useForm<FormData>();

  const onSubmit: SubmitHandler<FormData> = (data) => {
    const existingPlayer = players.find(
      (player) => player.name.trim().toLowerCase() === data.playerName.trim().toLowerCase()
    );
    if (!existingPlayer) {
      dispatch(gameActions.addPlayer({ name: data.playerName }));
    }
    navigate("/game");
  };

  return (
    <div className="start-page">
      <h2>Ласкаво просимо до гри "Вгадай Слово"</h2>
      <form onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="playerName">Введіть ваше ім'я:</label>
        <input
          id="playerName"
          {...register("playerName", { required: "Ім'я є обов'язковим" })}
        />
        {errors.playerName && <p className="error-message">{errors.playerName.message}</p>}
        <button type="submit">Почати гру</button>
      </form>
    </div>
  );
};

export default StartPage;