import { useEffect, useState } from "react";
import GameSetup from "../components/GameSetup";
import GameInfo from "../components/GameInfo/GameInfo";
import { useSelector, gameActions, useDispatch } from "../store";
import data from "../data/data.json";

const getRandomEntry = () => data[Math.floor(Math.random() * data.length)];
const baseTime = 30;
const maxAttempts = 6;

const GameContent = () => {
  const dispatch = useDispatch();
  const { word, hint, guessedLetters, incorrectGuesses, players } = useSelector(
    (state) => state.game
  );

  const [timeLeft, setTimeLeft] = useState(baseTime);
  const [gameStarted, setGameStarted] = useState(false);
  const [rounds, setRounds] = useState<number | null>(null);
  const [currentRound, setCurrentRound] = useState(1);
  const [gameOver, setGameOver] = useState(false);
  const [isGameWon, setIsGameWon] = useState(false);
  const [wordProcessed, setWordProcessed] = useState(false);

  useEffect(() => {
    if (!gameStarted || timeLeft === 0 || gameOver || isGameWon) return;
    const timer = setTimeout(() => setTimeLeft((prev) => prev - 1), 1000);
    return () => clearTimeout(timer);
  }, [timeLeft, gameStarted, gameOver, isGameWon]);

  useEffect(() => {
    if (timeLeft === 0 && gameStarted) {
      setGameOver(true);
      setGameStarted(false);
    }
  }, [timeLeft, gameStarted]);

  useEffect(() => {
    if (guessedLetters.length) {
      const wordGuessed = word
        .split("")
        .every((letter) => guessedLetters.includes(letter));

      if (incorrectGuesses >= maxAttempts) {
        setGameOver(true);
        setGameStarted(false);
      }

      if (wordGuessed && !wordProcessed) {
        setWordProcessed(true);
        setIsGameWon(true);
        setGameStarted(false); // Зупиняємо час при перемозі

        const currentPlayer = players[players.length - 1];
        if (currentPlayer) {
          dispatch(gameActions.incrementGuessedWords({ name: currentPlayer.name }));
        }

        // Якщо це останній раунд — завершити гру
        if (!rounds || currentRound >= rounds) {
          setGameOver(true);
        } else {
          // Дати користувачу побачити перемогу перед наступним словом
          setTimeout(() => {
            const newWord = getRandomEntry();
            dispatch(gameActions.setWord(newWord));
            setTimeLeft(baseTime);
            setCurrentRound((prev) => prev + 1);
            setWordProcessed(false);
            setIsGameWon(false);
            setGameStarted(true);
          }, 2000); // 2 секунди пауза для відображення перемоги
        }
      }
    }
  }, [guessedLetters, wordProcessed]);

  const restartGame = () => {
    dispatch(gameActions.resetGame());
    const newWord = getRandomEntry();
    dispatch(gameActions.setWord(newWord));
    setGameStarted(false);
    setGameOver(false);
    setRounds(null);
    setCurrentRound(1);
    setTimeLeft(baseTime);
    setIsGameWon(false);
    setWordProcessed(false);
  };

  if (!gameStarted) {
    return (
      <GameSetup
        totalRounds={rounds}
        setTotalRounds={setRounds}
        startGame={(count) => {
          setRounds(count);
          setGameStarted(true);
          dispatch(gameActions.setWord(getRandomEntry()));
          setTimeLeft(baseTime);
          setCurrentRound(1);
          setGameOver(false);
          setIsGameWon(false);
        }}
      />
    );
  }

  return (
    <GameInfo
      currentRound={currentRound}
      totalRounds={rounds ?? 1}
      word={word}
      hint={hint}
      incorrectGuesses={incorrectGuesses}
      maxAttempts={maxAttempts}
      timeLeft={timeLeft}
      isGameWon={isGameWon}
      gameOver={gameOver}
      restart={restartGame}
    />
  );
};

export default GameContent;
