import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface GameState {
  word: string;
  hint: string;
  guessedLetters: string[];
  incorrectGuesses: number;
  players: { name: string; guessedWords: number }[];
}

const initialState: GameState = {
  word: '',
  hint: '',
  guessedLetters: [],
  incorrectGuesses: 0,
  players: [],
};

const gameSlice = createSlice({
  name: 'game',
  initialState,
  reducers: {
    setWord(state, action: PayloadAction<{ word: string; hint: string }>) {
      state.word = action.payload.word;
      state.hint = action.payload.hint;
      state.guessedLetters = [];
      state.incorrectGuesses = 0;
    },
    guessLetter(state, action: PayloadAction<string>) {
      const letter = action.payload;
      if (!state.guessedLetters.includes(letter)) {
        state.guessedLetters.push(letter);
        if (!state.word.includes(letter)) {
          state.incorrectGuesses++;
        }
      }
    },
    resetGame(state) {
      state.word = '';
      state.hint = '';
      state.guessedLetters = [];
      state.incorrectGuesses = 0;
    },
    addPlayer(state, action: PayloadAction<{ name: string }>) {
      state.players.push({ name: action.payload.name, guessedWords: 0 });
    },
    incrementGuessedWords(state, action: PayloadAction<{ name: string }>) {
      const player = state.players.find(p => p.name === action.payload.name);
      if (player) {
        player.guessedWords++;
      }
    },
    removePlayer(state, action: PayloadAction<{ name: string }>) {
      state.players = state.players.filter(p => p.name !== action.payload.name);
    },
  },
});

export const gameActions = gameSlice.actions;
export default gameSlice.reducer;
